import { Component } from '@angular/core';

import { IonicPage, NavParams, ViewController } from 'ionic-angular';

@IonicPage()
@Component({
	selector: 'page-menu-filter',
	templateUrl: 'menu-filter.html'
})
export class MenuFilterPage {
	categories: Array<{name: string, isChecked: boolean}> = [];

	constructor(
		public navParams: NavParams,
		public viewCtrl: ViewController
	) {
		// passed in array of category names that should be excluded (unchecked)
		let excludedCategoryNames = this.navParams.data.excludeCategories;
		let fixedMenu = this.navParams.data.fixedMenu;

		fixedMenu.forEach((category: any) => {
			this.categories.push({
				name: category.name,
				isChecked: (excludedCategoryNames.indexOf(category.name) === -1)
			});
		});
	}

	resetFilters() {
		// reset all of the toggles to be checked
		this.categories.forEach(category => {
			category.isChecked = true;
		});
	}

	applyFilters() {
		// Pass back a new array of category names to exclude
		let excludedCategoryNames = this.categories.filter(c => !c.isChecked).map(c => c.name);
		this.dismiss(excludedCategoryNames);
	}

	dismiss(data?: any) {
		// using the injected ViewController this page
		// can "dismiss" itself and pass back data
		this.viewCtrl.dismiss(data);
	}
}



